.contact {
  width: 100%;
  height: calc(100% - 140px);
  top: 70px;
  padding: 5%;
  display: flex;
  justify-content: center;
  align-items: center;
  .mailbox {
    position: absolute;
    width: 136px;
    height: 144px;
    background-image: url(../assets/images/send.png);
    background-size: cover;
    background-repeat: no-repeat;
    bottom: 0;
  }
  .main {
    width: 80%;
    max-width: 600px;
    height: 360px;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-wrap: nowrap;
    gap: 5%;
    margin-bottom: 8vh;
    .imgBox {
      position: absolute;
      width: 100%;
      aspect-ratio: 18/11;
      max-width: 630px;
      max-height: 385px;
      left: 50%;
      top: 50%;
      transform: translate(-50%, -50%);
      .mailCover {
        width: 100%;
        height: 100%;
        background-image: url("../assets/images/mailcover.png");
        background-size: cover;
        transform: rotate(5deg);
      }
    }
    form {
      height: 100%;
      width: 95%;
      background-color: rgba(238, 238, 238, 0.8);
      color: #333;
      padding: 20px;
      box-shadow: 5px 5px 25px rgba(0, 0, 0, 0.3);
      h4 {
        margin: 5px 0px;
      }
      label {
        display: inline-block;
        margin: 8px 0px;
        font-weight: 600;
        span {
          color: red;
        }
      }
      input,
      textarea {
        font-size: 1.2rem;
        display: block;
        border: solid 3px #333;
      }
      input {
        width: 60%;
      }
      textarea {
        width: 100%;
      }
      button {
        cursor: pointer;
        left: 50%;
        transform: translateX(-50%);
        padding: 5px 15px;
        margin-top: 2vh;
        color: #fff;
        font-size: 1rem;
        font-weight: 600;
        border: 4px solid #fff;
        border-radius: 6px;
        background-color: #ffc800;
        &:hover {
          background-color: #eee;
          color: #333;
        }
      }
    }
    .information {
      display: flex;
      flex-direction: column;
      align-items: center;
      gap: 4vh;
      width: 40px;
      height: 100%;
      background-color: #e0e0e0;
      color: #333;
      padding: 20px;
      box-shadow: 5px 5px 25px rgba(0, 0, 0, 0.3);
      h2 {
        width: fit-content;
        margin: 0;
        writing-mode: vertical-rl;
      }
      #icons {
        display: flex;
        flex-direction: column;
        gap: 1vh;
        a {
          text-align: center;
          color: #1f4ead;
          font-size: 1.6rem;
          transition: 0.2s;
        }
        a:hover {
          transform: translateY(-2px) scale(1.1);
          color: #3570f1;
        }
      }
    }
  }

  .bubble {
    position: absolute;
    bottom: 10%;
    width: 120px;
    padding: 1vh 1vw;
    background: #fff;
    border-radius: 6px;
    text-align: center;
    box-shadow: 2px 2px 5px #333;
    &:before {
      content: "";
      position: absolute;
      top: 60%;
      left: -20px;
      border-top: 10px solid transparent;
      border-bottom: 10px solid transparent;
      border-right: 20px solid #fff;
    }
  }
  .bubble1 {
    left: 40px;
    animation: pop 6s 2s both;
  }
  .bubble2 {
    animation: pop2 4s 47s both;
  }

  .character {
    position: absolute;
    background-image: url("../assets/images/Lulu.png");
    background-repeat: no-repeat;
    background-size: cover;
    width: 144px;
    height: 192px;
    left: -100px;
    bottom: 0;
    transform: translateX(-50%) rotate(10deg);
    animation: peek 10s 10s 3, peek2 10s 40s;
  }
}

@keyframes pop {
  0%,
  100% {
    opacity: 0;
  }
  10%,
  90% {
    opacity: 100;
  }
}

@keyframes pop2 {
  0% {
    left: 50px;
    opacity: 0;
  }
  10%,
  90% {
    left: 40px;
    opacity: 100;
  }
  100% {
    left: 40px;
    opacity: 0;
  }
}

@keyframes peek {
  0%,
  100% {
    left: -100px;
  }
  90% {
    left: 0;
  }
}

@keyframes peek2 {
  0%,
  30%,
  80%,
  100% {
    left: -100px;
  }
  20% {
    left: 0px;
  }
  70% {
    left: -20px;
  }
}
